import React, {useEffect,useState} from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import loginbg from "../assets/img/login-bg.jpg";
import logo from "../assets/img/logo.png";
// import { AppBar } from '@material-ui/core';
// import { Toolbar } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import { API_BASE_URL } from '../App/Config/connection';
import axios from 'axios';
import validator from 'validator';
import Alert from '@material-ui/lab/alert';
import swal from 'sweetalert';
import {SendEmailVerification} from '../Helpers.js'

const queryString = require('query-string')

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright © '}
      <Link color="inherit" href="https://material-ui.com/">
        endlessfr8
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  root: {
    height: '100vh',
  },
  image: {
    backgroundImage: `url(${loginbg})`,
    backgroundRepeat: 'no-repeat',
    backgroundColor:
      theme.palette.type === 'light' ? theme.palette.grey[50] : theme.palette.grey[900],
    backgroundSize: 'cover',
    backgroundPosition: 'center',
  },
  paper: {
    margin: theme.spacing(8, 4),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));
class ForgotPassword extends React.Component { 
  constructor(props,location) {
    super(props);
    
    this.state = { 
      email : "",

  }
  this.SendEmailVerification = SendEmailVerification.bind(this)

  }
  componentDidMount() {
    let self = this;
  }

  render() {
  
    return (
      <ForgotPassword1 email_v_token={this.state.email_v_token}  history={this.props.history} prop={this.props} parent={this} />
    )}
}
 function ForgotPassword1(props) {
  const classes = useStyles();
  
  // const myValueFromProp = props;
  // const urlParams = new URLSearchParams(location.search);
  const [state , setState] = useState({
    email : "",

});


  const handleChange = (e) => {
    const {id , value} = e.target   
    console.log(id,"=====",value)
    setState(prevState => ({
        ...prevState,
        [id] : value
    }))
}

const handleSubmit = (e) => {
  e.preventDefault();
  
  const errors = [];
  setState({ errors:[], successmsg:'' });
  console.log("====successmsg=====",state.successmsg,"====successmsg");
  
  if(!validator.isEmail(state.email)){
    errors.push('Please enter valid email');
  }
 if(state.email.length) {
      //props.showError(null);
      const payload={
          "email":state.email
      }
      axios.post(API_BASE_URL+'/user/forgot-password', payload)
          .then(function (response) {
              if(response.status === 200){
                setState({ successmsg: "Password reset link has been sent to your email !"});
                
                swal("Success", "Password reset link has been sent to your email !", "success");
                props.history.push('/login');
                
              }
              else if(response.status === 202){
               // errors.push('Please verify your email!');
                
                swal({
                  title: "Email Verification",
                  text: "Please verify your email!",
                  icon: "warning",
                  dangerMode: true,
                  buttons: {
                    confirm: {
                      text: "OK",
                      value: true,
                      visible: true,
                      className: "",
                      closeModal: true
                    },
                    cancel: {
                      text: "Resend Email",
                      value: false,
                      visible: true,
                      className: "",
                      closeModal: true,
                    }
                  }
                })
                .then( async ok => {
                  if (!ok) {
                    const res = await props.parent.SendEmailVerification(response.data.email);
                    console.log(res,"====SendEmailVerification")
                  }
                });
                
                // setState({ errors, email_verification_required:true });
                // console.log(response.data.msg,"===response");
              } else{
                errors.push(response.data.msg);
                setState({ errors });
                console.log(response.data.msg,"===response");
              }
          })
          .catch(function (error) {
              console.log(error);
          });    
  } else {  
      errors.push('Please enter valid username and password');
      setState({ errors });
  }


  
}
  return (
    <Grid container component="main" className={classes.root}>
      <CssBaseline />
      {/* <AppBar color="inherit">
    <Toolbar>
        <img scr={logo} alt="logo" className={classes.logo} />
      </Toolbar>
</AppBar> */}
      <Grid item xs={false} sm={4} md={7} className={classes.image} >
        <img src={logo} style={{'margin': '24px 32px', 'height': '58px'}}/>

<div style={{'fontSize': '26px','fontWeight': 'bolder','color': 'rgb(255, 255, 255)','border': '10px solid rgb(254,181,55,.5)', 'padding': '10px 12px', 'textAlign': 'center','textShadow': '0px 0px 28px #f1f1f2',
    'bottom': '60px', width:'86%', marginLeft:'7%', marginTop:'392px'
}}>The Next Revolution In Trucking Is Here.</div>
      </Grid>
      <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
        <div className={classes.paper}>
          <Avatar className={classes.avatar}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Forgot Password
            {/* <img src={logo} /> */}
          </Typography>
          <form className={classes.form}  onSubmit={handleSubmit}>
          {state.errors && state.errors.map(error => (
          <Alert severity="error" key={error}>{error}</Alert>
        ))}
        {
          (state.successmsg && state.successmsg.trim()!=='' && state.successmsg!== undefined && state.successmsg!== null) ?
        <Alert severity="success" key="successmsg">{state.successmsg}</Alert>
        : ''
        }
        {state.email_v_token}
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              autoComplete="email"
              autoFocus
              onChange={handleChange}
              value={state.email || ''}
            />
          
           
            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
            >
              Submit
            </Button>
            <Grid container>
              <Grid item xs>
                <Link href="#login" variant="body2">
                  Login
                </Link>
              </Grid>
              <Grid item>
                <Link href="#signup" variant="body2">
                  {"Don't have an account? Sign Up"}
                </Link>
              </Grid>
            </Grid>
            <Box mt={5}>
              <Copyright />
            </Box>
          </form>
        </div>
      </Grid>
    </Grid>
  );
}
export default withStyles(useStyles)(ForgotPassword);