import { Types } from '../constants/actionTypes';

const initialState = {
  adminReducer: {
  }
}

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case Types.USER_LOGOUT:
    console.log('login', action.payload.user)
      return {
        ...state,
        adminReducer: ''
      }
    default:
      return state;
  }
}

export default reducer;