
import { API_BASE_URL } from './App/Config/connection';
import axios from 'axios';
import swal from 'sweetalert';
export function SendEmailVerification(email){
 
    const payload={
        "email":email
    }
      axios.post(API_BASE_URL+'/user/resend-email-verification', payload)
              .then(function (response) {
                  if(response.status === 200){
                    console.log("===emailsent");
                    swal("Email Sent!", "A verification link has been sent to you email id. Please verify you email id to continue.", "success");
                    return "A verification link has been sent to you email id. Please verify you email id to continue.";
                  } else{
                    swal("Failed", "Something went wrong", "error");
                    return "Something went wrong";
                  
                  }
              })
              .catch(function (error) {
                swal("Failed", "Something went wrong", "error");
                   return"Something went wrong";
                  console.log(error,"====errors");
              }); 
   
  }