import React, {useState} from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import loginbg from "../assets/img/login-bg.jpg";
import logo from "../assets/img/logo.png";
// import { AppBar } from '@material-ui/core';
// import { Toolbar } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import { API_BASE_URL } from '../App/Config/connection';
import axios from 'axios';
import validator from 'validator';
import Alert from '@material-ui/lab/alert';


function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright © '}
      <Link color="inherit" href="https://material-ui.com/">
        endlessfr8
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  root: {
    height: '100vh',
  },
  image: {
    backgroundImage: `url(${loginbg})`,
    backgroundRepeat: 'no-repeat',
    backgroundColor:
      theme.palette.type === 'light' ? theme.palette.grey[50] : theme.palette.grey[900],
    backgroundSize: 'cover',
    backgroundPosition: 'center',
  },
  paper: {
    margin: theme.spacing(8, 4),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

class SignUp extends React.Component { 
  constructor(props) {
    super(props)
    this.state = { 
      email : "",
      first_name : "",
      last_name : "",
      phone : "",
      password : "",
      confirm_password : "",
      errors: [],
      successmsg:''
  }
    
  }
  

  render() {
  
    return (
      <SignUp1 />
    )}
}
 function SignUp1(props) {
  const classes = useStyles();

  const [state , setState] = useState({
    email : "",
    first_name : "",
    last_name : "",
    phone : "",
    password : "",
    confirm_password : "",
    errors: [],
    successmsg:''
});

  const handleChange = (e) => {
    const {id , value} = e.target   
    console.log(id,"=====",value)
    setState(prevState => ({
        ...prevState,
        [id] : value
    }))
}

const handleSubmit = (e) => {
  e.preventDefault();
  
  const errors = [];
  setState({ errors:[], successmsg:'' });
  console.log("====successmsg=====",state.successmsg,"====successmsg");
  
  if(!validator.isEmail(state.email)){
    errors.push('Please enter valid email');
  }
  if(!validator.isMobilePhone(state.phone)){
    errors.push('Please enter valid phone');
    console.log(state.phone,"valid phone")
  }
  // if(!validator.isStrongPassword(state.password)){
  //   errors.push('Password must include minLength: 8, minLowercase: 1, minUppercase: 1, minNumbers: 1, minSymbols: 1');
  // }
   else if(state.password !== state.confirm_password)  {
    errors.push('Passwords do not match');
  }
  else{

  }

  if (errors.length > 0) {
    setState({ errors });
    //return;
  }
  else sendDetailsToServer();
}
const sendDetailsToServer = () => {
  const errors = [];
  if(state.email.length && state.password.length) {
      //props.showError(null);
      const payload={
          "email":state.email,
          "password":state.password,
          "first_name":state.first_name,
          "last_name":state.last_name,
          "phone":state.phone
      }
      axios.post(API_BASE_URL+'/user/signup', payload)
          .then(function (response) {
              if(response.status === 200){
                setState({ successmsg: "A verification link has been sent to you email id. Please verify you email id to continue."});
                errors.push();
                  // setState(prevState => ({
                  //     ...prevState,
                  //     'successMessage' : 'Registration successful. Redirecting to home page..'
                  // }))
                  //redirectToHome();
                  //props.showError(null)
              } else{
                errors.push(response.data.msg);
                setState({ errors });
                console.log(response.data.msg,"===response");
              }
          })
          .catch(function (error) {
              console.log(error);
          });    
  } else {  
      errors.push('Please enter valid username and password');
      setState({ errors });
  }
 
  
}

  return (
    <Grid container component="main" className={classes.root}>
      <CssBaseline />
      {/* <AppBar color="inherit">
    <Toolbar>
        <img scr={logo} alt="logo" className={classes.logo} />
      </Toolbar>
</AppBar> */}
      <Grid item xs={false} sm={4} md={7} className={classes.image} >
        <img src={logo} style={{'margin': '24px 32px', 'height': '58px'}}/>

<div style={{'fontSize': '26px','fontWeight': 'bolder','color': 'rgb(255, 255, 255)','border': '10px solid rgb(254,181,55,.5)', 'padding': '10px 12px', 'textAlign': 'center','textShadow': '0px 0px 28px #f1f1f2',
    'bottom': '60px', width:'86%', marginLeft:'7%', marginTop:'392px'
}}>The Next Revolution In Trucking Is Here.</div>
      </Grid>
      <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square style={{'height': '100%', 'overflow-y': 'scroll'}} >
        <div className={classes.paper}>
          <Avatar className={classes.avatar}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign Up
            {/* <img src={logo} /> */}
          </Typography>
          <form className={classes.form}   onSubmit={handleSubmit}>
          {state.errors && state.errors.map(error => (
          <Alert severity="error" key={error}>{error}</Alert>
        ))}
        {
          (state.successmsg && state.successmsg.trim()!=='' && state.successmsg!== undefined && state.successmsg!== null) ?
        <Alert severity="success" key="successmsg">{state.successmsg}</Alert>
        : ''
        }
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="first_name"
              label="First Name"
              name="first_name"
              autoComplete="first_name"
              autoFocus
              onChange={handleChange}
              value={state.first_name || ''}
             
            />
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="last_name"
              label="Last Name"
              name="last_name"
              autoComplete="last_name"
              autoFocus
              onChange={handleChange}
              value={state.last_name || ''}
              
            />
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              autoComplete="email"
              type="email"
              autoFocus
              onChange={handleChange}
              value={state.email || ''}
              
              // helperText={ "Name needs to be 'a'" }
              // error={!validator.isEmail(state.email) ?  true : false}
            />
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="phone"
              label="Phone"
              name="phone"
              autoComplete="phone"
              autoFocus
              onChange={handleChange}
              value={state.phone || ''}
              
            />
            {/* <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="company"
              label="Company"
              name="company"
              autoComplete="company"
              autoFocus
            /> */}
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              autoComplete="current-password"
              onChange={handleChange}
              value={state.password || ''}
              
            />
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="confirm_password"
              label="Confirm Password"
              type="password"
              id="confirm_password"
              autoComplete="confirm_password"
              onChange={handleChange}
              value={state.confirm_password || ''}
              
            />
            {/* <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="dl"
              label="DL No."
              name="dl"
              autoComplete="dl"
              autoFocus
            />
             <TextField
              id="dl_expiry"
              name="dl_expiry"
              label="DL Expiry"
              type="date"
              defaultValue="2017-05-24"
              className={classes.textField}
              fullWidth
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="home_address"
              label="Home Address"
              name="home_address"
              autoComplete="home_address"
              autoFocus
            />
           
           <TextField
              id="DOB"
              name="DOB"
              label="DOB"
              type="date"
              defaultValue="2017-05-24"
              className={classes.textField}
              fullWidth
              InputLabelProps={{
                shrink: true,
              }}
            />
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="Endorcements"
              label="Endorcements"
              name="Endorcements"
              autoComplete="Endorcements"
              autoFocus
            />
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="IMGLicense"
              label="IMGLicense"
              name="IMGLicense"
              autoComplete="IMGLicense"
              autoFocus
            />
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="IMGEndorcements"
              label="IMGEndorcements"
              name="IMGEndorcements"
              autoComplete="IMGEndorcements"
              autoFocus
            /> */}
            {/* <FormControlLabel
              control={<Checkbox value="remember" color="primary" />}
              label="Remember me"
            /> */}
            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
            >
              Sign Up
            </Button>
            <Grid container>
              {/* <Grid item xs>
                <Link href="#" variant="body2">
                  Forgot password?
                </Link>
              </Grid> */}
              <Grid item>
                <Link href="#login" variant="body2">
                  {"Already have an account? Sign In"}
                </Link>
              </Grid>
            </Grid>
            <Box mt={5}>
              <Copyright />
            </Box>
          </form>
        </div>
      </Grid>
    </Grid>
  );
}

export default withStyles(useStyles)(SignUp);